@import url("https://fonts.googleapis.com/css2?family=Rajdhani:wght@500;600;700&&display=swap");
html {
  font-size: 16px;
}

body {
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}

h1, p {
  margin: 0;
}

.main {
  background-color: #111111;
}

.description {
  color: white;
  font-family: "Roboto", sans-serif;
  margin: 0.8rem 0 1rem;
  font-size: 1rem;
}

.container .title {
  margin-top: 2rem;
}

@media (min-width: 720px) {
  html {
    font-size: 18px;
  }
}
@media (min-width: 920px) {
  html {
    font-size: 20px;
  }
}
.container {
  min-width: 80%;
  max-width: 80%;
  margin: 0 auto;
}

section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main .title {
  font-family: "Bebas Neue", sans-serif;
  text-transform: uppercase;
  color: white;
  font-size: 4rem;
}

.social-links a {
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
}

.social-links {
  display: flex;
  gap: 0.7rem;
}

