.main .container {
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 2;
}

.main .photo {
  max-width: 30%;
}

.main .photo img {
  width: 100%;
  height: auto;
}

.main .text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.main canvas {
  position: absolute;
  z-index: 1;
}

.section-menu {
  background-color: black;
  display: flex;
}

.main {
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  max-height: 100vh;
  overflow-y: scroll;
  min-height: 100vh;
}

section {
  scroll-snap-align: start;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}

.menu-content {
  transform: translate3d(-100px, 0, 0);
  opacity: 0;
  transition: all 1s ease;
}

.active-menu .menu-content {
  transform: none;
  opacity: 1;
}

.menu-item {
  font-family: "Bebas Neue", sans-serif;
  text-transform: uppercase;
  color: white;
  font-size: 4rem;
  display: block;
  text-decoration: none;
  cursor: pointer;
  margin-bottom: 1.5rem;
  transition: all 0.4s ease;
}

.menu-item:hover {
  transform: translate3d(50px, 0, 0);
}

