@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes hide {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@media (max-width: 768px) {
  .main .photo {
    display: none;
  }
}
.photos-container .photo-wrapper {
  flex: 0 31.8%;
  min-width: 200px;
  margin-top: 2.3%;
  cursor: pointer;
  object-fit: cover;
  overflow: hidden;
}

.photos-container img {
  object-fit: cover;
  transition: 0.4s;
  width: 100%;
}

.photos-container .photo-wrapper:hover img {
  transform: scale(1.1);
}

.photos-container {
  display: flex;
  gap: 2.3%;
  flex-wrap: wrap;
}

.pagination {
  margin: 1rem auto;
}

.pagination .pagination-btn {
  width: 30px;
  height: 30px;
  font-size: 17px;
  border: 2px solid white;
  background: none;
  color: white;
  cursor: pointer;
  outline: none;
}

.pagination .page-number {
  color: white;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 2px;
  font-size: 16px;
}

.pagination .page-number.active {
  font-weight: bold;
}

.pagination-btn[disabled] {
  color: #b8b8b8;
  border-color: #b8b8b8;
  cursor: pointer;
}

.image-preloader {
  height: 52px;
  width: 52px;
  background-color: #ccc;
  background-image: linear-gradient(90deg, #ddd 0px, #e8e8e8 40px, #ddd 80px);
  background-size: 300px;
  animation: shine-avatar 1.6s infinite linear;
}

@keyframes shine-avatar {
  0% {
    background-position: -32px;
  }
  40%, 100% {
    background-position: 208px;
  }
}